body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 50px;
  box-sizing: border-box;
}

.app-counter {
  margin-bottom: 40px;
  line-height: calc(100vh - 300px);
  font-size: 80vh;
  color: black;
  text-align: center;
}
.app-counter--positive {
  color: blue;
}
.app-counter--negative {
  color: red;
}

.app-actions {
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 90%;
}

.app-actions__buton { 
  -webkit-flex: 1 1; 
          flex: 1 1;
  margin: 0 20px;
}

